import react from 'react'
import cx from 'classnames'

import projecttabsStyles from './styles/projecttabs.module.scss'

class ProjectTabs extends react.Component {

    render() {
        console.log(this.props.data)
        return (
            <div className={projecttabsStyles.projectsContainer}>
                {this.props.data.map((child, idx) => {
                    return (
                        <div key={idx} className={cx(projecttabsStyles.projectTabContainer, this.props.active === child.key ? projecttabsStyles.active : null)}>
                            <div className={projecttabsStyles.tabBox}
                                key={idx}
                                onClick={() => this.props.onChange(child.key)}
                            >
                                {child}
                            </div>
                        </div>
                    )
                })
                }
            </ div >
        )
    }
}

export default ProjectTabs;